<template>
  <!-- playList.length player -->
  <div class="player" v-if="playList.length > 0">
    <!-- fullScreen : true  normal -->
    <transition name="normal">
      <div class="normal-player" v-show="fullScreen">
        <div class="background">
          <img width="100%" height="100%" :src="currentSong.albummid" />
        </div>
        <!--  -->
        <div class="top">
          <div class="back" @click="backMini">
            <i class="icon-back"></i>
          </div>
          <h1 class="title">{{ currentSong.songname }}</h1>
          <h2 class="subtitle">{{ currentSong.singer }}</h2>
        </div>
        <!-- CD -->
        <div
          class="middle"
          @touchstart.prevent="lyricStart"
          @touchmove.prevent="lyricMove"
          @touchend="lyricEnd"
        >
          <!-- CD -->
          <div class="middle-l" ref="middleL" v-show="true">
            <div class="cd-wrapper">
              <div class="cd" :class="rotateCD">
                <img class="image" :src="currentSong.albummid" />
              </div>
            </div>
            <!--  -->
            <div class="playing-lyric-wrapper">
              <div class="playing-lyric">{{ playingLyric }}</div>
            </div>
          </div>
          <!--  -->
          <scroll class="middle-r" ref="lyricList">
            <div class="lyric-wrapper">
              <div v-if="currentLyric">
                <!--  -->
                <p
                  v-for="(item, index) in currentLyric.lines"
                  class="text"
                  :key="index"
                  :class="{'current':currentLine === index}"
                  ref="lyricLine"
                >{{ item.txt }}</p>
              </div>
              <div v-else></div>
            </div>
          </scroll>
        </div>
        <!--  -->
        <div class="bottom">
          <!-- CD  -->
          <div class="dot-wrapper">
            <span class="dot" :class="{'active': currentShow=== 'CD'}"></span>
            <span class="dot" :class="{'active': currentShow === 'lyric'}"></span>
          </div>
          <!--  -->
          <div class="progress-wrapper">
            <span class="time time-l">{{ formatTime(currentTime) }}</span>
            <div class="progress-bar-wrapper">
              <!--  -->
              <progress-bar :percent="percent" @percentChange="changePerc"></progress-bar>
            </div>
            <span class="time time-r">{{formatTime(currentSong.interval)}}</span>
          </div>
          <!--  -->
          <div class="operators">
            <!--  -->
            <div class="icon i-left" @click="changeMode">
              <i :class="iconMode"></i>
            </div>
            <div class="icon i-left">
              <i class="icon-prev" @click="prevSong"></i>
            </div>
            <div class="icon i-center">
              <i :class="normalIcon" @click="togglePlay"></i>
            </div>
            <div class="icon i-right">
              <i class="icon-next" @click="nextSong"></i>
            </div>
            <div class="icon i-right">
              <i class="icon-not-favorite"></i>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <!-- fullScreen : false  mini -->
    <transition name="mini">
      <div class="mini-player" v-show="!fullScreen" @click="openNormal">
        <div class="icon">
          <img width="40" height="40" :src="currentSong.albummid" :class="rotateCD" />
        </div>
        <div class="text">
          <h2 class="name">{{ currentSong.songname }}</h2>
          <p class="desc">{{ currentSong.singer }}</p>
        </div>
        <!--  -->
        <div class="control">
          <i :class="miniIcon" @click.stop="togglePlay"></i>
        </div>
        <div class="control">
          <i class="icon-playlist"></i>
        </div>
      </div>
    </transition>
    <!--  -->
    <audio
      :src="currentSong.url"
      ref="audio"
      @canplay="readyPlay"
      @error="playErr"
      @timeupdate="timeUpdate"
      @ended="playEnd"
    ></audio>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import progressBar from "./progress-bar";
import { playMode } from "@common/js/paramConf";
import LyricParser from "lyric-parser";
import scroll from "@base/m-scroll/m-scroll";
export default {
  data() {
    return {
      songReady: false,
      currentTime: 0,
      currentLyric: null,
      currentLine: null,
      currentShow: "CD",
      playingLyric: ""
    };
  },
  computed: {
    ...mapGetters('player',[
      "playList",
      "fullScreen",
      "currentSong",
      "playing",
      "currentIndex",
      "playMode",
      "orderList"
    ]),
    //  playing  mini
    miniIcon() {
      return this.playing ? "icon-pause-mini" : "icon-play-mini";
    },
    // normal
    normalIcon() {
      return this.playing ? "icon-pause" : "icon-play";
    },
    // 
    iconMode() {
      return this.playMode === playMode.order
        ? "icon-sequence"
        : this.playMode === playMode.loop
        ? "icon-loop"
        : "icon-random";
    },
    rotateCD() {
      return this.playing ? "play" : "play pause";
    },
    //   = currentTime / interval
    percent() {
      return this.currentTime / this.currentSong.interval;
    }
  },
  methods: {
    ...mapMutations('player',[
      "setFullScreen",
      "setPlaying",
      "setCurrentIndex",
      "setPlayMode",
      "setPlayList"
    ]),
    //  mini  normal
    openNormal() {
      //  mutations type setFullScreen 
      this.setFullScreen(true);
    },
    backMini() {
      this.setFullScreen(false);
    },
    // 
    togglePlay() {
      if (!this.songReady) {
        return;
      }
      //  setPlaying ,  playing 
      this.setPlaying(!this.playing);

      // 
      if (this.currentLyric) {
        this.currentLyric.togglePlay();
      }
    },
    // 
    prevSong() {
      // 

      if (!this.songReady) {
        return;
      }
      let index = this.currentIndex - 1;
      if (index === -1) {
        index = this.playList.length - 1;
      }
      this.setCurrentIndex(index);
      if (!this.playing) {
        this.togglePlay();
      }
      this.songReady = false;
    },
    nextSong() {
      // 

      // 
      if (!this.songReady) {
        return;
      }
      let index = this.currentIndex + 1;
      // indexlength
      if (index === this.playList.length) {
        index = 0;
      }
      // currentIndex, setCurrentIndex
      this.setCurrentIndex(index);
      // 
      if (!this.playing) {
        this.togglePlay();
      }
      //  songReady
      this.songReady = false;
    },
    readyPlay() {
      this.songReady = true;
    },
    playErr() {
      this.songReady = false;
    },
    // 
    timeUpdate(e) {
      // 
      this.currentTime = e.target.currentTime;
    },
    //  currentTime => 00 : 00  90
    formatTime(time) {
      time = Math.floor(time);
      let m = Math.floor(time / 60);
      let s = time % 60;
      if (m.toString().length < 2) {
        m = "0" + m;
      }
      if (s.toString().length < 2) {
        s = "0" + s;
      }
      return `${m}:${s}`;
    },
    //   audio.currentTime
    changePerc(newPerc) {
      //  
      let currentTime = this.currentSong.interval * newPerc;
      this.$refs.audio.currentTime = currentTime;
      //  
      if (this.currentLyric) {
        this.currentLyric.seek(currentTime * 1000);
      }
    },
    // 
    playEnd() {
      //   
      if (this.playMode === playMode.loop) {
        // currentTime 0
        this.$refs.audio.currentTime = 0;
        // 
        this.$refs.audio.play();
      } else {
        // 
        this.nextSong();
      }
    },
    //  playMode
    changeMode() {
      //   0 1 2 
      let mode = (this.playMode + 1) % 3;
      // console.log(mode)
      this.setPlayMode(mode);
      let list = null;
      // ,
      if (this.playMode === playMode.random) {
        list = this.randomList(this.orderList);
      } else {
        list = this.orderList;
      }
      //  currentIndex
      this.resetcIndex(list);
      //  mutations setPlayList 
      this.setPlayList(list);
    },
    // 
    randomList(arr) {
      let _arr = arr.slice();
      for (let i = 0; i < _arr.length; i++) {
        //  0 ~ i 
        let r = Math.floor(Math.random() * (i + 1));
        //  _arr[i] | _arr[r]
        let val = _arr[i];
        _arr[i] = _arr[r];
        _arr[r] = val;
      }
      return _arr;
    },
    //  currentIndex
    resetcIndex(list) {
      let cindex = list.findIndex(item => {
        // item => song
        return item.songid === this.currentSong.songid;
      });
      //  mutation ,  currentIndex
      console.log(cindex);
      this.setCurrentIndex(cindex);
    },
    //    
    getLyric() {
      //  
      this.currentSong
        ._getLyric()
        .then(lyric => {
          this.currentLyric = new LyricParser(lyric, this.handleLyric);
          //  
          if (this.playing) {
            this.currentLyric.play();
          }
        })
        .catch(err => {
          console.log(err);
          this.currentLyric = null;
          this.playingLyric = null;
          this.currentLine = 0;
        });
    },
    handleLyric({ lineNum, txt }) {
      this.currentLine = lineNum;
      //  elem
      if (lineNum > 5) {
        let elem = this.$refs.lyricLine[lineNum - 5];
        //  scroll 
        this.$refs.lyricList.scrollToElement(elem, 1000);
      } else {
        // 
        this.$refs.lyricList.scrollTo(0, 0, 200);
      }
      this.playingLyric = txt;
    },
    //    touch
    lyricStart(e) {
      // touchstart
      this.touch.isStart = true;
      // 
      this.touch.isMove = false;
      const t = e.touches[0];
      //  start X Y 
      this.touch.startX = t.pageX;
      this.touch.startY = t.pageY;
    },
    lyricMove(e) {
      if (!this.touch.isStart) {
        return;
      }

      let t = e.touches[0];
      //   end - start  = distanceX | distanceY
      let distanceX = t.pageX - this.touch.startX;
      let distanceY = t.pageY - this.touch.startY;
      //  
      // distanceY > distanceX 
      if (Math.abs(distanceY) > Math.abs(distanceX)) {
        return;
      }

      if (!this.touch.isMove) {
        this.touch.isMove = true;
      }
      //  0,   -window.innnerWidth
      let left = this.currentShow === "CD" ? 0 : -window.innerWidth;

      let offsetWidth = Math.min(
        0,
        Math.max(-window.innerWidth, left + distanceX)
      );
      //   transform: translateX
      this.$refs.lyricList.$el.style.transform = `translateX(${offsetWidth}px)`;

      //  percent
      this.touch.percent = Math.abs(offsetWidth / window.innerWidth);
      //  CD 
      this.$refs.middleL.style.opacity = 1 - this.touch.percent;
    },
    lyricEnd(e) {
      // 
      // CD
      if (!this.touch.isMove) {
        return;
      }
      let offsetWidth;
      let opacity;
      if (this.currentShow === "CD") {
        // 20%
        if (this.touch.percent > 0.2) {
          offsetWidth = -window.innerWidth;
          opacity = 0;
          this.currentShow = "lyric";
        } else {
          // 
          offsetWidth = 0;
          opacity = 1;
        }
      } else {
        if (this.touch.percent < 0.9) {
          offsetWidth = 0;
          opacity = 1;
          this.currentShow = "CD";
        } else {
          offsetWidth = -window.innerWidth;
          opacity = 0;
        }
      }
      //   
      this.$refs.lyricList.$el.style.transform = `translateX(${offsetWidth}px)`;
      // 
      this.$refs.lyricList.$el.style.transition = "all 0.3s ease-out";
      this.$refs.middleL.style.opacity = opacity;

      this.touch.isStart = false;
    }
  },
  watch: {
    //  currentSong 
    currentSong(newSong, oldSong) {
      //  getLyric
      if (newSong.songid === oldSong.songid) {
        return;
      }
      // 
      if (this.currentLyric) {
        this.currentLyric.stop();
        this.currentTime = 0;
        this.currentLine = 0;
      }
      this.$nextTick(() => {
        const audio = this.$refs.audio;
        // audiosrc play()
        setTimeout(() => {
          audio.play(); // 
          this.getLyric(); // 
        }, 200);
      });
    },
    //  playing 
    playing(newVal) {
      // playing   
      this.$nextTick(() => {
        // DOM
        const audio = this.$refs.audio;
        newVal ? audio.play() : audio.pause();
      });
    }
    //   playMode 
  },
  created() {
    //  touch 
    this.touch = {};
  },
  components: {
    progressBar,
    scroll
  }
};
</script>

<style scoped lang="stylus" >
@import '../../common/stylus/variable';
@import '../../common/stylus/mixin';

.player {
  .normal-player {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 150;
    background: $color-background;

    .background {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      opacity: 0.9;
      filter: blur(40px);
    }

    .top {
      position: relative;
      margin-bottom: 25px;

      .back {
        position: absolute;
        top: 0;
        left: 6px;
        z-index: 50;

        .icon-back {
          display: block;
          padding: 9px;
          font-size: $font-size-large-x;
          color: $color-theme;
          transform: rotate(-90deg);
        }
      }

      .title {
        width: 70%;
        margin: 0 auto;
        line-height: 40px;
        text-align: center;
        no-wrap();
        font-size: $font-size-large;
        color: #f2f2f2;
      }

      .subtitle {
        line-height: 20px;
        text-align: center;
        font-size: $font-size-medium;
        color: #f2f2f2;
      }
    }

    .middle {
      position: fixed;
      width: 100%;
      top: 80px;
      bottom: 170px;
      white-space: nowrap;
      font-size: 0;

      .middle-l {
        display: inline-block;
        vertical-align: top;
        position: relative;
        width: 100%;
        height: 0;
        padding-top: 80%;

        .cd-wrapper {
          position: absolute;
          left: 10%;
          top: 0;
          width: 80%;
          height: 100%;

          .cd {
            width: 100%;
            height: 100%;
            box-sizing: border-box;
            border: 10px solid rgba(255, 255, 255, 0.1);
            border-radius: 50%;

            &.play {
              animation: rotate 20s linear infinite;
            }

            &.pause {
              animation-play-state: paused;
            }

            .image {
              position: absolute;
              left: 0;
              top: 0;
              width: 100%;
              height: 100%;
              border-radius: 50%;
            }
          }
        }

        .playing-lyric-wrapper {
          width: 80%;
          margin: 30px auto 0 auto;
          overflow: hidden;
          text-align: center;

          .playing-lyric {
            height: 20px;
            line-height: 20px;
            font-size: $font-size-medium;
            color: #f9f9f9;
          }
        }
      }

      .middle-r {
        display: inline-block;
        vertical-align: top;
        width: 100%;
        height: 100%;
        overflow: hidden;

        .lyric-wrapper {
          width: 80%;
          margin: 0 auto;
          overflow: hidden;
          text-align: center;

          .text {
            line-height: 32px;
            color: rgba(250,250,250,.7);
            font-size: $font-size-medium-x;

            &.current {
              color: #fff;
            }
          }
        }
      }
    }

    .bottom {
      position: absolute;
      bottom: 20px;
      width: 100%;

      .dot-wrapper {
        text-align: center;
        font-size: 0;

        .dot {
          display: inline-block;
          vertical-align: middle;
          margin: 0 4px;
          width: 8px;
          height: 8px;
          border-radius: 50%;
          background: $color-text-l;

          &.active {
            width: 20px;
            border-radius: 5px;
            background: $color-text-ll;
          }
        }
      }

      .progress-wrapper {
        display: flex;
        align-items: center;
        width: 86%;
        margin: 0px auto;
        padding: 10px 0;

        .time {
          color: #f2f2f2;
          font-size: $font-size-small;
          flex: 0 0 30px;
          line-height: 30px;
          width: 30px;

          &.time-l {
            text-align: left;
          }

          &.time-r {
            text-align: right;
          }
        }

        .progress-bar-wrapper {
          flex: 1;
          margin: 0 10px;
        }
      }

      .operators {
        display: flex;
        align-items: center;

        .icon {
          flex: 1;
          color: $color-theme;

          &.disable {
            color: $color-theme-d;
          }

          i {
            font-size: 30px;
          }
        }

        .i-left {
          text-align: right;
        }

        .i-center {
          padding: 0 20px;
          text-align: center;

          i {
            font-size: 40px;
          }
        }

        .i-right {
          text-align: left;
        }

        .icon-favorite {
          color: $color-sub-theme;
        }
      }
    }

    &.normal-enter-active, &.normal-leave-active {
      transition: all 0.4s;

      .top, .bottom {
        transition: all 0.4s cubic-bezier(0.86, 0.18, 0.82, 1.32);
      }
    }

    &.normal-enter, &.normal-leave-to {
      opacity: 0;

      .top {
        transform: translate3d(0, -100px, 0);
      }

      .bottom {
        transform: translate3d(0, 100px, 0);
      }
    }
  }

  .mini-player {
    display: flex;
    align-items: center;
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 180;
    width: 100%;
    height: 60px;
    background: #fff;
    box-shadow: 0 -5px 10px rgba(0,0,0,.1);
    &.mini-enter-active, &.mini-leave-active {
      transition: all 0.4s;
    }

    &.mini-enter, &.mini-leave-to {
      opacity: 0;
    }

    .icon {
      flex: 0 0 40px;
      width: 40px;
      padding: 0 10px 0 20px;

      img {
        border-radius: 50%;

        &.play {
          animation: rotate 10s linear infinite;
        }

        &.pause {
          animation-play-state: paused;
        }
      }
    }

    .text {
      display: flex;
      flex-direction: column;
      justify-content: center;
      flex: 1;
      line-height: 20px;
      overflow: hidden;

      .name {
        margin-bottom: 2px;
        no-wrap();
        font-size: $font-size-medium;
        color: $color-text;
      }

      .desc {
        no-wrap();
        font-size: $font-size-small;
        color: $color-text-d;
      }
    }

    .control {
      flex: 0 0 30px;
      width: 30px;
      padding: 0 10px;

      .icon-play-mini, .icon-pause-mini, .icon-playlist {
        font-size: 30px;
        color: #31c27c;
      }

      .icon-mini {
        font-size: 32px;
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
</style>