<template>
  <div ref="wrapper">
    <slot></slot>
  </div>
</template>
<script>
import BScroll from "better-scroll";
export default {
  props: {
    //  
    // 1 scroll 
    // 2 scroll 
    // 3  scroll 
    probeType: {
      // 
      type: Number,
      default: 1
    },
    data: {
      type: Array,
      default() {
        return [];
      }
    },
    pullup: {
      type: Boolean,
      default: false
    },
    listenScroll: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    _initScroll() {
      // 
      if (!this.$refs.wrapper) {
        return;
      }
      // 1.  2. options
      this.scroll = new BScroll(this.$refs.wrapper, {
        probeType: this.probeType,
        click: true,
        // 
        momentum:true,
        //   ms
        swipeTime: 1500,
        //   number
        deceleration: 0.003
      }); 
      // scroll
      if(this.listenScroll) {
        // scroll
        this.scroll.on('scroll', (prop)=>{
          this.$emit('scroll', prop)
        })
      }
    },
    enable() {
      // BScroll
      this.scroll && this.scroll.enable();
    },
    disable() {
      //  BScroll
      this.scroll && this.scroll.disable();
    },
    refresh() {
      this.scroll && this.scroll.refresh();
    },
    scrollToElement(elem, ms) {
      //  elem  , ms : 
      this.scroll && this.scroll.scrollToElement(elem, ms);
    },
    //  
    scrollTo(x, y, ms) {
      this.scroll && this.scroll.scrollTo(x, y, ms);
    } 
  },
  mounted() {
    this.$nextTick(() => {
      //  BScroll DOM
      this._initScroll();
    });
  },
  watch: {
    data() {
      setTimeout(() => {
        this.refresh();
      }, 20);
    }
  }
};
</script>
