<template>
  <div class="progress-bar" ref="progressBar">
    <!--  -->
    <div class="bar-inner" @click='clickProgress'>
      <!--   -->
      <div class="progress" ref="progress"></div>
      <div class="progress-btn-wrapper" ref="progressBtn"
        @touchstart.prevent='touchStart'
        @touchmove.prevent='touchMove'
        @touchend='touchEnd'
      >
        <!--  -->
        <div class="progress-btn"></div>
      </div>
    </div>
  </div>
</template>

<script >
export default {
  created(){
    // touch
    this.touches = {}
  },
  props: {
    percent: { //  
      type: Number,
      default: 0
    }
  },
  methods: {
    touchStart(e){
      //  flag  isStart 
      this.touches.isStart = true;
      //  
      this.touches.startX = e.touches[0].pageX;
      // 
      this.touches.offsetWidth = this.$refs.progress.clientWidth; 
    },
    touchMove(e){
      //  isStart 
      if(!this.touches.isStart){
        return;
      }
      //  end - start
      const distanceX = e.touches[0].pageX - this.touches.startX;
      //  =  + distanceX
      const left =  Math.min(this.$refs.progressBar.clientWidth - 16,  Math.max(0, this.touches.offsetWidth + distanceX));
      // 
      this._offset(left)
    },
    touchEnd(e){
      // isStart  false 
      this.touches.isStart = false; 
      //  
      this._triggerPercent()
    },
    _triggerPercent(){
      //   
      const barWidth = this.$refs.progressBar.clientWidth - 16;
      const percent = this.$refs.progress.clientWidth / barWidth;
      // percent m-player
      this.$emit('percentChange',percent)
    },
    _offset(o){
       // 
      this.$refs.progressBtn.style.transform = 
                                   `translateX(${o}px)`;
      // 
      this.$refs.progress.style.width = `${o}px`;
    },
    // 
    clickProgress(e){
      this. _offset(e.offsetX);
      this._triggerPercent()
    } 
  },
  watch: {
    // 
    percent(newPerc){
      // watch
      if(this.touches.isStart){
        return;
      }
      //   =  * () 
      const barWidth =  this.$refs.progressBar.clientWidth - 16;
      const offsetWidth = barWidth * newPerc;
      // 
      this._offset(offsetWidth)
    }
  }
};
</script>

<style scoped lang="stylus">
@import '~@common/stylus/variable';

.progress-bar {
  height: 30px;

  .bar-inner {
    position: relative;
    top: 13px;
    height: 4px;
    background: rgba(255, 255, 255, 0.5);

    .progress {
      position: absolute;
      height: 100%;
      background: $color-theme;
    }

    .progress-btn-wrapper {
      position: absolute;
      left: -8px;
      top: -13px;
      width: 30px;
      height: 30px;

      .progress-btn {
        position: relative;
        top: 7px;
        left: 7px;
        box-sizing: border-box;
        width: 16px;
        height: 16px;
        border: 3px solid #fff;
        border-radius: 50%;
        background: #fff;
      }
    }
  }
}
</style>