/*  
  musicData 
  albummid , albumname , songid, songmid, songname, singer 
    
*/ 
// 
import getLyric from '@api/lyric';
import {  Base64 } from 'js-base64';
class Song {
  constructor({albummid, albumname, songid, songmid, songname, singer,interval, url}) {
    this.albumname = albumname; // 
    this.songid = songid;     // id
    this.songmid = songmid;   // 
    this.songname = songname; // 
    this.singer = filterSinger(singer); // 
    this.albummid = `http://y.gtimg.cn/music/photo_new/T002R300x300M000${albummid}.jpg?max_age=2592000`; // 
    this.url = url;
    this.interval = interval;
    // 
  }
  // song lyric
  _getLyric(){
    // 
    if(this.lyric){
      return Promise.resolve(this.lyric)
    }
    //  promsie  
    return new Promise((resolve, reject)=> {
      getLyric(this.songmid).then(res=> {
        if(res.code === 0){
          // res.lyric base64 Base64.decode()
          this.lyric = Base64.decode(res.lyric);
          resolve(this.lyric);
        }else{
          reject('lyric is not defined')
        }
      })
    })
  }
}
// 
function filterSinger(s){
  let ret = [];
  if(!s){
    return;
  }
  s.forEach(item=>{
    ret.push(item.name)  
  })
  return ret.join(' / ') 
}

export default Song;