import { mapGetters } from 'vuex';
export const playlistMixin = {
  computed: {
    ...mapGetters('player',['playList'])
  },
  mounted() {
    this.handlePlaylist(this.playList)
  },
  // 
  activated() {
    this.handlePlaylist(this.playList)
  },
  watch: {
    // playList 
    playList(newVal) {
      this.handlePlaylist(newVal)
    }
  },
  methods: {
    handlePlaylist(){
      throw new Error('handlePlaylist must be defined')
    }
  },
}